.container .card .icon {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 10%;
    background: #2c73df;
}
.container .card .icon .fa {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 80px;
    color: #fff;
}
.container .card .slide {
    width: 221px;
    height: 140px;
    transition: 0.5s;
}
.container .card .slide.slide1 {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    transition: .7s;
    transform: translateY(100px);
}
.container .card:hover .slide.slide1{
    transform: translateY(70px);
}
.container .card .slide.slide2 {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    box-sizing: border-box;
    transition: .8s;
    transform: translateY(-20px);
    box-shadow: 0 20px 40px rgba(90, 89, 89, 0.4);
}
.container .card:hover .slide.slide2{
    transform: translateY(60px);
}
.container .card .slide.slide2::after{
    content: "";
    position: absolute;
    width: 30px;
    height: 4px;
    bottom: 10px;
    transform: translateX(-0%);
    background: #2c73df;
}
.container .card .slide.slide2 .content p {
    margin: 0;
    padding: 0;
    text-align: center;
    color: #414141;
}
.container .card .slide.slide2 .content h3 {
    margin: 0 0 10px 0;
    padding: 0;
    font-size: 24px;
    text-align: center;
    color: #414141;
} 